/* Button */
.li-button{
    position: absolute;
    top: 20px;
    right: 20px;
    cursor: pointer;
    background: rgba(177, 177, 177, 0.3);
    transition: background .3s ease;
    border-radius: 10px;
    height: 50px;
}
.li-button:hover{
    background: rgba(134, 134, 134, 0.7);
}
.li-button-icon{
    width: 30px;
    height: 30px;
    padding: 10px;
}

/* Dialogue */
.li-dialogue{
    top: 0;
    width: 100%;
    height: 100%; 
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 99;
    background: #0005;
}
.li-dialogue-container{
    background: white;
    padding-left: 40px;
    padding-right: 40px;
    padding-top: 20px;
    padding-bottom: 20px;
    border-radius: 10px;
    width: 500px;
    display: flex;
    overflow: hidden;
    align-items: center;
    box-sizing: revert;
}
.li-title{
    margin: 0;
    margin-bottom: 30px;
}
.li-login-container{
    flex: 0 0 100%;
    margin-right: 50px;
    transition: margin .5s ease;
    box-sizing: revert;
}
.li-register-container{
    flex: 0 0 100%;
}
.li-register-toggle {
    text-decoration: underline;
    cursor: pointer;
    color: #999;
}

.li-form-input{
    width: 100%;
    padding: 8px 12px;
    margin-top: 5px;
    margin-bottom: 10px;
    display: inline-block;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
    font-size: 15px;
}
.li-input-info {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.li-form-title {
    text-align: left;
    margin: 0px;
    position: relative;
    top: 13px;
    left: 15px;
    background-color: #fff;
    padding: 0 5px;
    font-size: 13px;
}
.li-input-err {
    text-align: right;
    margin: 0px;
    font-size: 13px;
    color: #b40000;
    position: relative;
    top: 32px;
    margin-right: 12px;
}
.li-form-button {
    width: 100%;
    background-color: #000;
    transition: background .3s ease;
    color: white;
    padding: 14px;
    margin: 20px 0;
    border: none;
    border-radius: 50px;
    cursor: pointer;
}
.li-form-button:hover{
    background-color: #300600;
}
.li-error{
    color: red;
}


/* Transitions */
.fade-enter {
