.switchSelector {
    display: flex;
    width: 100%;
    margin: 20px 0;
}
.ss-icon{
    margin-left: 30px;
    font-size: 30px;
}
.ss-name{
    margin-left: 20px;
    font-size: 18px;
    align-self: center;
}
.ss-switch {
    position: relative;
    display: inline-block;
    margin-left: auto;
    margin-right: 30px;
    width: 40px;
    height: 23px;
}
.ss-switch input {
    opacity: 0;
    width: 0;
    height: 0;
}
.switchSelector .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #707070;
    transition: .4s;
    transition: .4s;
}
.slider:before {
    position: absolute;
    content: "";
    height: 17px;
    width: 17px;
    left: 4px;
    bottom: 3px;
    background-color: #F8F9FA;
    transition: .4s;
    transition: .4s;
}
input:checked + .slider {
    background-color: black;
}
input:focus + .slider {
    box-shadow: 0 0 1px black;
}
input:checked + .slider:before {
    transform: translateX(16px);
    transform: translateX(16px);
    transform: translateX(16px);
}
.slider.round {
    border-radius: 34px;
}
.slider.round:before {
    border-radius: 50%;
}